// 画像アスペクト比対応
.bg-img-zoom {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;
  // @extend .img-zoom;
}

.img-bg50 {
  position: relative;

  &::before {
    display: block;
    content: "";
    padding-top: 50%;
  }
}

.img-bg60 {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  width: 100%;

  &::after {
    display: block;
    content: "";
    padding-top: 60%;
  }
}
